import { useRef } from 'react';
import { useReactToPrint } from 'react-to-print';
import tw from 'tailwind-styled-components';

const PrintButton = tw.button`py-2 px-4 bg-blue-500 text-white font-semibold rounded-md shadow-md`;

const PrintDiv = tw.div`p-4 rounded-md`;

const CommonPrintComponent = ({ content }) => {
  const printRef = useRef();

  const handlePrint = useReactToPrint({
    content: () => printRef.current,
  });

  return (
    <div>
      <PrintDiv ref={printRef}>{content}</PrintDiv>
      <div className='flex justify-center mt-10'>
        <PrintButton onClick={handlePrint} className='bg-indigo-500 hover:bg-indigo-600 text-xl px-10 py-2 mb-10 text-white rounded shadow'>Print</PrintButton>
      </div>
    </div>
  );
};

export default CommonPrintComponent;